"use client";
import { LineChart, Line, Pie, PieChart, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend, Cell } from 'recharts';
import CustomTooltip from './CustomTooltip';
import SalesByCategoryPie from './SalesByCategoryPie';
import { DollarSign, TrendingUp, Users, ShoppingBag, Calendar, MapPin, BarChart3 } from "lucide-react";

interface ChartDataPoint {
    date: string;
    value: number;
    quantity: number;
    tonnage: number;
}
interface CategoryDataPoint {
    category: string;
    quantity: number;
    tonnage: number;
    salesValue: number;
}
interface Props {
    salesTrendData: ChartDataPoint[];
    sales: any[];
    productList: any[]; // Add productList prop
}

interface LocationDataPoint {
    location: string;
    quantity: number;
    salesValue: number;
}

export default function SalesTrendChart({ salesTrendData, sales, productList }: Props) {
    if (!salesTrendData.length || !sales.length || !productList.length) {
        return <p>No sales data available.</p>;
    }

    // Transform data for category bar chart (year-to-date)
    const categories = Array.from(
        new Set(
            sales
                .filter(sale => new Date(sale.saleDate).getFullYear() === new Date().getFullYear())
                .map(sale => sale.product?.category || 'Uncategorized')
        )
    ).sort();

    const categoryData = categories.map(category => {
        const categorySales = sales
            .filter(sale =>
                new Date(sale.saleDate).getFullYear() === new Date().getFullYear() &&
                sale.product?.category === category
            );
        return {
            category,
            tonnage: categorySales.reduce((sum, sale) => {
                const product = productList.find(p => p.id === sale.productId);
                const weightInTons = product ? (product.weightValue * sale.quantity * product.packSize) / 1000 : 0;
                return sum + weightInTons;
            }, 0),
            quantity: categorySales.reduce((sum, sale) => sum + sale.quantity, 0),
            salesValue: categorySales.reduce((sum, sale) => sum + sale.quantity * sale.salePrice, 0),
        };
    }).sort((a, b) => b.tonnage - a.tonnage);

    // Transform data for location bar chart (year-to-date)
    const locations = Array.from(
        new Set(
            sales
                .filter(sale => new Date(sale.saleDate).getFullYear() === new Date().getFullYear())
                .map(sale => sale.location?.name || 'Unknown')
        )
    );

    const locationData = locations.map(location => {
        const locationSales = sales
            .filter(sale => new Date(sale.saleDate).getFullYear() === new Date().getFullYear() && sale.location?.name === location);
        return {
            location,
            quantity: locationSales.reduce((sum, sale) => sum + sale.quantity, 0),
            salesValue: locationSales.reduce((sum, sale) => sum + sale.quantity * sale.salePrice, 0),
            tonnage: locationSales.reduce((sum, sale) => {
                const product = productList.find(p => p.id === sale.productId);
                const weightInTons = product ? (product.weightValue * sale.quantity * product.packSize) / 1000 : 0;
                return sum + weightInTons;
            }, 0),
        };
    }).sort((a, b) => b.tonnage - a.tonnage);

    return (
        <div className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Sales Value Trend */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <BarChart3 className="h-5 w-5 text-green-500" color='blue' />
                        Sales Value Trend
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Sales Value (K)</h4>
                    <ResponsiveContainer width="100%" height={200}>
                        <LineChart data={salesTrendData}>
                            <XAxis dataKey="date" />
                            <YAxis />
                            <Tooltip content={<CustomTooltip labels={{
                                titleKey: 'date',
                                metrics: [
                                    { key: 'tonnage', label: 'Tonnage', format: (v) => `${(v ?? 0).toFixed(2)} tons` },
                                    { key: 'value', label: 'Sales Value', format: (v) => `K${(v ?? 0).toFixed(2)}` },
                                    { key: 'quantity', label: 'Quantity', format: (v) => `${v} units` },

                                ],
                            }} />} />
                            <Line type="monotone" dataKey="value" stroke="#2563eb" strokeWidth={2} />
                        </LineChart>
                    </ResponsiveContainer>
                </div>
                {/* Sales Tonnage Trend */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <BarChart3 className="h-5 w-5 text-green-500" color='green' /> Sales Tonnage Trend
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Tonnage</h4>
                    <ResponsiveContainer width="100%" height={200}>
                        <LineChart data={salesTrendData}>
                            <XAxis dataKey="date" />
                            <YAxis />
                            <Tooltip content={<CustomTooltip labels={{
                                titleKey: 'date',
                                metrics: [
                                    { key: 'tonnage', label: 'Tonnage', format: (v) => `${(v ?? 0).toFixed(2)} tons` },
                                    { key: 'value', label: 'Sales Value', format: (v) => `K${(v ?? 0).toFixed(2)}` },
                                    { key: 'quantity', label: 'Quantity', format: (v) => `${v} units` },
                                ],
                            }} />} />
                            <Line type="monotone" dataKey="tonnage" stroke="#16a34a" strokeWidth={2} />
                        </LineChart>
                    </ResponsiveContainer>
                </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                {/* Sales Value by Location Pie Chart*/}
                <SalesByCategoryPie categoryData={categoryData} />

                {/* Sales Value by Location */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <MapPin className="h-5 w-5 text-blue-500" /> Sales Value by Location (Year-to-Date)
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Sales Value</h4>
                    <ResponsiveContainer width="100%" height={250}>
                        <BarChart data={locationData} >

                            <XAxis dataKey="location" />
                            <YAxis
                                tickCount={10}
                                domain={[0, 'dataMax + 1000']}
                                tickFormatter={(value) => value.toFixed(0)} />
                            // For Sales by Location
                            <Tooltip content={<CustomTooltip labels={{
                                titleKey: 'location',
                                metrics: [
                                    { key: 'tonnage', label: 'Tonnage', format: (v) => `${(v ?? 0).toFixed(2)} tons` },
                                    { key: 'salesValue', label: 'Sales Value', format: (v) => `K${(v ?? 0).toFixed(2)}` },
                                    { key: 'quantity', label: 'Quantity', format: (v) => `${v} units` },

                                ],
                            }} />} />
                            <Bar
                                dataKey="salesValue"
                                name="salesValue"
                                label={(props) => {
                                    const { x, y, width, height, value } = props;
                                    return (
                                        <text
                                            x={x + width / 2}
                                            y={y - 5}
                                            fill="#666"
                                            textAnchor="middle"
                                            dominantBaseline="middle"
                                        >
                                            {`K${value.toFixed(2)}`}
                                        </text>
                                    );
                                }}
                            >
                                {locationData.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill="#3b82f6" />
                                ))}
                            </Bar>
                        </BarChart>
                    </ResponsiveContainer>
                </div>

                {/* Sales by Location */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <MapPin className="h-5 w-5 text-blue-500" /> Sales by Location (Year-to-Date)
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Tonnage</h4>
                    <ResponsiveContainer width="100%" height={250}>
                        <BarChart data={locationData} >

                            <XAxis dataKey="location" />
                            <YAxis
                                tickCount={10}
                                domain={[0, 'dataMax + 0.3']}
                                tickFormatter={(value) => value.toFixed(2)} />
                            // For Sales by Location
                            <Tooltip content={<CustomTooltip labels={{
                                titleKey: 'location',
                                metrics: [
                                    { key: 'tonnage', label: 'Tonnage', format: (v) => `${(v ?? 0).toFixed(2)} tons` },
                                    { key: 'salesValue', label: 'Sales Value', format: (v) => `K${(v ?? 0).toFixed(2)}` },
                                    { key: 'quantity', label: 'Quantity', format: (v) => `${v} units` },

                                ],
                            }} />} />
                            <Bar
                                dataKey="tonnage"
                                name="Tonnage"
                                label={(props) => {
                                    const { x, y, width, height, value } = props;
                                    return (
                                        <text
                                            x={x + width / 2}
                                            y={y - 5}
                                            fill="#666"
                                            textAnchor="middle"
                                            dominantBaseline="middle"
                                        >
                                            {`${value.toFixed(2)} tons`}
                                        </text>
                                    );
                                }}
                            >
                                {locationData.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill="#16a34a" />
                                ))}
                            </Bar>
                        </BarChart>
                    </ResponsiveContainer>
                </div>
            </div>
        </div >
    );
}
